.section-1{background-image:-webkit-gradient(linear, left top, left bottom, from(#c1e1ee), to(#9ad9e7));background-image:linear-gradient(to bottom, #c1e1ee, #9ad9e7)}.section-2{background-color:white}.section-3{background-color:#c1e1ee;padding-bottom:30px;overflow:hidden}.section-4{background-color:white;margin-bottom:30px}

nav p{-webkit-transition:0.15s ease-in;transition:0.15s ease-in}nav p:hover{transform:scale(1.1);-ms-transform:scale(1.1);-webkit-transform:scale(1.1)}nav .navbar-brand{padding-left:20px}nav .nav-item{padding:0.9em}nav .nav-item:last-child{padding-right:5em}nav p{color:#2d8db3;font-size:20px;font-family:"Josefin Sans",sans-serif}@media (max-width: 768px){.navbar{background:#c1e1ee}}

.jumbotron,.jumbotron .container{display:flex;justify-content:center;align-items:center;margin:0px;padding:0px;overflow:hidden}.jumbotron .container{-webkit-transition:0.15s ease-in;transition:0.15s ease-in}.jumbotron .container:hover{transform:scale(1.1);-ms-transform:scale(1.1);-webkit-transform:scale(1.1)}.jumbotron{height:60vh;background:transparent}.jumbotron .container{overflow:visible}.jumbotron img{width:55%;height:55%}@media (max-width: 768px){.jumbotron img{width:80%;height:80%}}@media (max-width: 576px){.jumbotron{height:50vh}.jumbotron img{width:100%;height:100%}}

.container-fluid{display:flex;justify-content:center;align-items:center;margin:0px;padding:0px;overflow:hidden}.container-fluid img{width:40%;height:40%}@media (max-width: 768px){.container-fluid img{width:55%;height:55%}}@media (max-width: 576px){.container-fluid img{width:100%;height:100%}}

.about .left-col,.about .right-col h1{display:flex;justify-content:center;align-items:center;margin:0px;padding:0px;overflow:hidden}.about .left-col{-webkit-transition:0.15s ease-in;transition:0.15s ease-in}.about .left-col:hover{transform:scale(1.1);-ms-transform:scale(1.1);-webkit-transform:scale(1.1)}.about .left-col img{width:60%;height:auto;margin:40px 0px;border-radius:10px}.about .right-col{overflow:hidden}@media (min-width: 768px) and (max-width: 1199px){.about .right-col{padding:30px 0px}}.about .right-col h1{color:#2d8db3;font-size:30px;font-family:"Nunito",sans-serif;font-weight:bold}.about .right-col p{color:#545454;font-size:18px;font-family:"Nunito",sans-serif;margin:20px 0px}

.footer .socialButtons,.footer .copyright{display:flex;justify-content:center;align-items:center;margin:0px;padding:0px;overflow:hidden}.footer .socialButtons .social{margin:10px}.footer .copyright{margin:0px 10px}.footer .copyright p{text-align:center;color:#9b9b9b}.footer .copyright .heart{color:red}

.social a{display:flex;justify-content:center;align-items:center;margin:0px;padding:0px;overflow:hidden}.social a{width:60px;height:60px;text-decoration:none;font-size:18px;color:white}.social a::before{width:60px;height:60px;content:"";position:absolute;background:linear-gradient(45deg, #60dae6, #9b9dc7);border-radius:50%;z-index:-1;-webkit-transition:0.3s ease-in;transition:0.3s ease-in}.social a:hover::before{-webkit-transform:scale(0);transform:scale(0)}.social a i{-webkit-transition:0.3s ease-in;transition:0.3s ease-in}.social a:hover i{background:linear-gradient(45deg, #60dae6, #9b9dc7);background-clip:text;-webkit-background-clip:text;-webkit-text-fill-color:transparent;-webkit-transform:scale(2.2);transform:scale(2.2)}

.breaker img{display:flex;justify-content:center;align-items:center;margin:0px;padding:0px;overflow:hidden}@media (min-width: 1200px){.breaker img{width:30%}}

.skills .skill{margin:10px 0px}

.skill,.skill p{display:flex;justify-content:center;align-items:center;margin:0px;padding:0px;overflow:hidden}.skill{-webkit-transition:0.15s ease-in;transition:0.15s ease-in}.skill:hover{transform:scale(1.1);-ms-transform:scale(1.1);-webkit-transform:scale(1.1)}.skill{width:auto;height:40px;background:white;border-radius:15px;border-bottom:3px solid #2d8db3}.skill p{font-family:"Nunito",sans-serif;font-size:23px;color:#2d8db3}

.projects .row{display:flex;justify-content:center;align-items:center;margin:0px;padding:0px;overflow:hidden}

.card .additional{display:flex;justify-content:center;align-items:center;margin:0px;padding:0px;overflow:hidden}.card .additional .more-info .fas{-webkit-transition:0.15s ease-in;transition:0.15s ease-in}.card .additional .more-info .fas:hover{transform:scale(1.1);-ms-transform:scale(1.1);-webkit-transform:scale(1.1)}.card{font-family:Abel, Arial, Verdana, sans-serif;width:450px;height:250px;background-color:white;border-radius:0px;overflow:hidden;border:none}.card .main{width:350px;height:100%;position:absolute;right:0;padding:1rem}.card .main h1{font-size:35px;text-align:center;color:#545454;padding:10px 0px 4px 0px}.card .additional{width:100px;height:100%;background:-webkit-gradient(linear, left top, left bottom, from(#de685e), to(#ee786e));background:linear-gradient(#de685e, #ee786e);-webkit-transition:width 0.4s;transition:width 0.4s;z-index:1}.card .additional .more-info .fas{font-size:400%;color:white}.card:hover .additional{width:100%}@media (max-width: 575px){.card{width:600px;height:200px}.card .additional{width:600px}}

::-webkit-scrollbar-track{border-radius:10px}::-webkit-scrollbar{width:10px}::-webkit-scrollbar-thumb{border-radius:10px;background-image:-webkit-gradient(linear, left bottom, left top, color-stop(0.44, #7a99d9), color-stop(0.72, #497dbd), color-stop(0.86, #2d6ab9))}

